@import "https://fonts.googleapis.com/css?family=Roboto:500&display=swap";
* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

h3, p, ul, button, body {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.container {
  justify-content: center;
  display: flex;
}

.card {
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  justify-content: center;
  align-self: center;
  height: 40px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  color: #00acdc;
  box-shadow: 0 4px #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-columns: 200px;
  gap: 46px 48px;
  width: fit-content;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1200px) {
  .catalog {
    padding: 50px 128px;
  }
}
